.header-container {
    padding-top: 50px;
    background-color: #d62e2e;
    margin-bottom: 35px;
}
.header-row {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-bottom: 2px solid #EEE;
    padding-bottom: 20px;
    color: #EEE;
}
.header-icon {
    font-size: 52px;
    margin-right: 20px;
}
h1 {
    font-size: 52px;
}
.green-text {
    color: #21ba45;
}

@media (max-width: 495px) {
    .header-icon {
        font-size: 40px;
        margin-right: 15px;
    }
    h1 {
        font-size: 40px;
    }
    .header-container {
        padding-top: 40px;
        margin-bottom: 25px;
    }
}
@media (max-width: 386px) {
    .header-icon {
        font-size: 30px;
        margin-right: 10px;
    }
    h1 {
        font-size: 30px;
    }
    .header-container {
        padding-top: 30px;
        margin-bottom: 20px;
    }
}

.timer-container {
    height: 250px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    background-color: #21ba453d;
    color: #21ba45;
}
.time-display-box {
    font-size: 92px;
    margin-left: auto;
    margin-right: auto;
}
.timer-box-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    padding-top: 40px;
    padding: 25px;
}
.timer-box-item {
    width: 50%;
}

@media (max-width: 991px) {
    .timer-box-container {
        padding: 20px;
    }
    .timer-box-item {
        width: 80%;
    }
}
@media (max-width: 495px) {
    .timer-box-container {
        padding: 10px;
    }
    .timer-box-item {
        width: 95%;
    }
}

body {
    background-color: #d62e2e;
}
